.theme {
    display: inline-block;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-size: 0;
  }
  
  .input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }
  
  .input:checked + .inner .box {
    background: #EE920C;
  }
  
  .input:checked + .inner .box:before {
    -webkit-transform: translate(20px, -50%);
            transform: translate(20px, -50%);
    background: #FCFCFD;
  }
  
  .inner {
    position: relative;
    display: flex;
    -webkit-transition: all .2s;
    transition: all .2s;
    margin-right:10px;
  }
  .darkicon{
      margin-top:9px;
      margin-right:5px;
  }
  .box {
    position: relative;
    display: block;
    width: 40px;
    height: 20px;
    background: #E6E8EC;
    border-radius: 10px;
    -webkit-transition: all .2s;
    transition: all .2s;
    margin-top:10px;
  }
  
  :global(.dark-mode) .box {
    background: #353945;
  }
  
  .box:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 4px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #EE920C;
    -webkit-transition: all .2s;
    transition: all .2s;
  }
  
  .themeBig .box {
    width: 48px;
    height: 24px;
    border-radius: 12px;
  }
  
  .themeBig .box:before {
    left: 4px;
    width: 16px;
    height: 16px;
  }
  
  .themeBig .input:checked + .inner .box:before {
    -webkit-transform: translate(24px, -50%);
            transform: translate(24px, -50%);
  }
  /*# sourceMappingURL=Theme.module.css.map */